<template>
  <div class="layout">
    <!-- <div class="top-nav"><span @click="change">切换</span></div>
    <div class="left-nav" @click="isuser" ref="leftNav">
      我是切换菜单
    </div> -->
    <router-view></router-view>

    <div class="layout-b">
      <van-tabbar v-model="active">
        <van-tabbar-item icon="home-o" to="/home">首页</van-tabbar-item>
        <van-tabbar-item icon="search" to="/shop">购物车</van-tabbar-item>
        <van-tabbar-item icon="friends-o"  to="/order">订单</van-tabbar-item>
        <van-tabbar-item icon="setting-o"  to="/vip">我的</van-tabbar-item>
      </van-tabbar>
    </div>
  </div>
</template>
<script>
import { Tabbar, TabbarItem } from "vant";
export default {
  data() {
    return {
      active:0,

    };
  },
  components: {
    [Tabbar.name]: Tabbar,
    [TabbarItem.name]: TabbarItem
  },
  methods:{
    change(){
      console.log("切换菜单");
      this.$refs.leftNav.style.color="#fff";
      this.$refs.leftNav.style.left=0+'px';
    },
    isuser(){
      console.log("我是系统菜单");
    }
  }
};
</script>
<style>

.layout-b {
  position: fixed;
  bottom: 0px;
}
.top-nav{
  text-align: left;
}
.left-nav{
  width: 100px;
  height: 100vh;
  position: fixed;
  top:0px;
  z-index: 10;
  left: -120px;
  background-color: red;
}

@keyframes myfirst
{
from {left:-120px;}
to {left: 0px;}
}

</style>




